@import url(//db.onlinewebfonts.com/c/5232693f6aa6f93e7927ff1b77102e57?family=Quatro+Sans);
@font-face {
	font-family: "Quatro Sans";
	src: url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.eot");
	src: url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.eot?#iefix")
			format("embedded-opentype"),
		url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.woff2")
			format("woff2"),
		url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.woff")
			format("woff"),
		url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.ttf")
			format("truetype"),
		url("//db.onlinewebfonts.com/t/5232693f6aa6f93e7927ff1b77102e57.svg#Quatro Sans")
			format("svg");
}

@font-face {
	font-family: "Open Sans";
	font-style: normal;
	font-weight: 300;
	src: local("Open Sans Light"), local("OpenSans-Light"),
		url(https://fonts.gstatic.com/s/opensans/v13/DXI1ORHCpsQm3Vp6mXoaTXhCUOGz7vYGh680lGh-uXM.woff)
			format("woff");
}
@font-face {
	font-family: "Open Sans";
	font-style: normal;
	font-weight: 400;
	src: local("Open Sans"), local("OpenSans"),
		url(https://fonts.gstatic.com/s/opensans/v13/cJZKeOuBrn4kERxqtaUH3T8E0i7KZn-EPnyo3HZu7kw.woff)
			format("woff");
}
@font-face {
	font-family: "Open Sans";
	font-style: normal;
	font-weight: 600;
	src: local("Open Sans Semibold"), local("OpenSans-Semibold"),
		url(https://fonts.gstatic.com/s/opensans/v13/MTP_ySUJH_bn48VBG8sNSnhCUOGz7vYGh680lGh-uXM.woff)
			format("woff");
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

#appRoot {
	display: flex;
	height: 100vh;
	font-family: "Open Sans";
	scroll-behavior: smooth;
	white-space: pre-line;
}

.page {
	padding-left: 15%;
	padding-top: 5%;
	width: 100%;
	box-sizing: border-box;
	overflow: scroll;
}

::-webkit-scrollbar {
	display: none;
}

.pageExpl {
	margin-bottom: 60px;
	max-width: 70%;
	/* word-wrap: break-word; */
	line-height: 1.5;
	font-weight: 300;
	text-align: justify;
	word-break: keep-all;
}

.pageTitle {
	font-family: Quatro Sans;
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 29px;

	color: #303030;
}

#pageSubtitle {
	font-weight: 300;
	font-size: 16px;
}

#projNavs {
	display: flex;
	flex-direction: column;
	text-align: left;
	margin-left: 25%;
	margin-top: 10%;
	margin-bottom: 10%;
}

/* #projNavs > a {
	padding: 3px;
} */

.projNav {
	padding: 3px;
	cursor: pointer;
}

#sidebar {
	/* background: linear-gradient(180deg, #08213d 68.23%, #000000 100%); */
	display: flex;
	flex-direction: column;
	background-size: auto 100%;
	width: 15vw;
	background: url("https://res.cloudinary.com/dunirybkl/image/upload/v1600082660/portfoli/asset_28b_4x_vxranw.png");
	background-size: cover;
	text-align: center;
	color: white;
}

#sidebar a {
	text-decoration: none;
	color: white;
}

#nlContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 50%;
}

.sbLink {
	padding: 5%;
	font-weight: bold;
}

#pitchImg {
	max-height: 80%;
}

.landingExpl {
	padding-bottom: 2.5%;
	border-bottom: 0.25px solid #000000;
}

.tiContainer {
	width: 75%;
	display: flex;
	justify-content: center;
	margin-top: 5%;
	margin-bottom: 5%;
}

.tiContainer > img {
	padding-left: 15px;
	padding-right: 15px;
	width: 40px;
	height: 40px;
}

.extsContainer {
	width: 75%;
	text-align: center;
	margin-top: 1%;
	margin-bottom: 3%;
	font-weight: bold;
}

.projectExpl {
	margin-bottom: 3%;
}

.projBannerContainer {
	width: 75%;
	display: flex;
	justify-content: center;
	box-sizing: border-box;
	margin-top: 3%;
	margin-bottom: 3%;
}

.projBannerContainer > video {
	outline: none;
}

.projBannerContainer > video:-webkit-full-screen {
	border: none;
}

.projBannerContainer > video:-ms-fullscreen {
	border: none;
}

.projBannerContainer > video:fullscreen {
	border: none;
}

.projBanner {
	margin: 0 auto;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

.phoneBanner {
	width: 25%;
	border-top: 10px solid #edeff1;
	border-bottom: 10px solid #edeff1;
	border-right: 12px solid #edeff1;
	border-left: 12px solid #edeff1;
	border-radius: 10%;
	border-bottom-right-radius: 30px;
	border-bottom-left-radius: 30px;
	border-top-right-radius: 30px;
	border-top-left-radius: 30px;
	/* box-shadow: 0px 20px 20px -20px rgba(0, 0, 0, 0.4); */
}

.desktopBanner {
	border-top: 14px solid #edeff1;
	border-bottom: 36px solid #edeff1;
	border-right: 9px solid #edeff1;
	border-left: 9px solid #edeff1;
	border-radius: 10%;
	border-bottom-right-radius: 30px;
	border-bottom-left-radius: 30px;
	border-top-right-radius: 30px;
	border-top-left-radius: 30px;
	/* box-shadow: 0px 20px 20px -20px rgba(0, 0, 0, 0.4); */
}



#contactContainer {
	height: 50%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

#contactTitle {
	text-align: center;
	width: 75%;
}

#contactExpl {
	text-align: center;
	max-width: 50%;
}

#ceContainer {
	display: flex;
	width: 75%;
	justify-content: center;
}

#ciContainer {
	width: 75%;
	display: flex;
	justify-content: center;
}

#ciContainer > a {
	padding-left: 15px;
	padding-right: 15px;
	width: auto;
}

